.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 32px 0 28px;
  max-width: 1440px;
}

.header__logo {
  display: flex;
  gap: 20px;
  align-items: center;
}

.header__avatar-logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__avatar-user {
  display: flex;
  gap: 20px;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  object-fit: cover;
  background-color: rgba(0, 0, 0, 0.5);
}

.header__avatar-logo-letter {
  position: absolute;
  font-family: "CabinetGrotesk-Bold";
  font-weight: 700;
  font-size: 20px;
  line-height: 24.8px;
  visibility: hidden;
}

.header__avatar {
  position: relative;
  display: flex;
  gap: 20px;
}

.date {
  font-family: "CabinetGrotesk-Bold";
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  font-style: normal;
}

.location {
  font-family: "CabinetGrotesk-Bold";
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  font-style: normal;
}

.username {
  font-family: "CabinetGrotesk-Bold";
  font-weight: 700;
  font-size: 20px;
  line-height: 24.8px;
}

.button {
  font-family: "CabinetGrotesk-Bold";
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  font-style: normal;
  border-width: 0;
  background-color: white;
  margin-top: -12px;
}
