.modal__submit-signup {
  display: flex;
  padding: 8px 12px;
  align-items: center;
  gap: 8px;
  border-radius: 4px;
  background: white;
  color: black;
  border: none;
  font-family: "CabinetGrotesk-Medium";
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 20px;
  width: 124px;
  height: 36px;
  background-color: white;
  color: rgba(0, 0, 0, 0.5);
}

.modal__content {
  background-color: white;
  max-width: 496px;
  width: 100%;
  border-radius: 20px;
  padding: 28px 28px 28px 36px;
  box-sizing: border-box;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  max-height: 436px;
  border: 1px solid rgba(0, 0, 0, 0.3);
  position: relative;
  font-family: "CabinetGrotesk-Medium";
  font-style: normal;
  font-weight: 700;
  line-height: 20px;
  font-size: 16px;
}
